<script>
import axios from 'axios';
export default {
	name: 'meteo',
	data() {
		return {
			info: null,
			city:'',
			currentCity:'',
			errorMsg: null,
			iconToday:'',
			iconTomorrow:'',
			iconAfter:'',
			temp:'metric',
			wind:'',
			tempType:[
				{name: 'tempC',value:true},
				{name: 'tempF', value:false}
			],
			background:[
				{name:"background-image:url(", url:require("../img/main.jpeg"), close:")"}
			],
			tipsImg:[
				{
					url: require('../img/tipcocktail.png'),
					alt:'cocktail',
					tip:'relax with some cocktails'},
				{
					url:require('../img/tipbeach.png'),
					alt:'beach',
					tip:'go to the beach'},
				{
					url:require('../img/tippark.png'),
					alt:'sun',
					tip:'play cards at the park (with friends)'},
				{
					url:require('../img/tipwind.png'),
					alt:'wind',
					tip:'watch a movie at home (and make pizza)'},
				{
					url:require('../img/tiprain.png'),
					alt:'rain',
					tip:'stay home and make some cookies'}
			]
		}
	},
	methods:{
		search(city){
			axios
			.get('http://api.openweathermap.org/data/2.5/forecast?q='+city+'&units='+this.temp+'&APPID=f25bf5a81ce95575f8e53560b64acdbc')
				.then(response => (
					this.info = response.data,
					this.iconToday = "https://openweathermap.org/img/w/"+response.data.list[0].weather[0].icon+".png",
					this.iconTomorrow = "https://openweathermap.org/img/w/"+response.data.list[8].weather[0].icon+".png",
					this.iconAfter = "https://openweathermap.org/img/w/"+response.data.list[16].weather[0].icon+".png"
					))
				.catch(function (error) {
					 if (error.response) {
						 alert('Error: city not found');
					 }
 				})
 			this.checkTempType();
 			this.currentCity=city;
 			this.city='';
		},
		changeTempType(temp){
			if(temp.name=="tempC"){
				this.tempType[1].value=temp.value;
				this.tempType[0].value=!temp.value;
			}
			else if(temp.name=="tempF"){
				this.tempType[0].value=temp.value;
				this.tempType[1].value=!temp.value;
			}
			this.checkTempType();
			if(this.currentCity!=''){
				this.search(this.currentCity);
			}
		},
		checkTempType(){
			//if the temperatue selected is Celsius
			if(this.tempType[0].value==true){
				this.temp='metric';
				this.wind='km';
			}
			//if the temperature selected is Fahrenheit
			else{
				this.temp='imperial';
				this.wind='miles';
			}
		},
		celsius(temp){
			//if the temperature selected is Fahrenheit
			if(this.tempType[1].value==true){
				return (temp-32)*5/9;
			}
			//if the temperatue selected is Celsius
			else{
				return temp;
			}
		}
	},
	computed: {
	  	chooseClass() {
		    if(this.info && this.info.list[0].weather[0].main=='Clear'){
				return 'sun';
			}
			else if(this.info && this.info.list[0].weather[0].main=='Clouds'){
				return 'clouds';
			}
			else if(this.info && this.info.list[0].weather[0].main=='Rain'){
				return 'rain';
			}
			else{
				return 'mainbackground';
			}
	  	}
	}
}
</script>

